import React, {
  useCallback, useEffect, useMemo, useState
} from 'react';
import {
  Box, Button, FormControl, FormLabel, Input, Text, Link, Flex, Checkbox, Select
} from '@chakra-ui/react';
import { Trans, useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';

import style from './SetupWallet.module.css';
import { createAccount } from '../../../../store/middlewares/account';
import { updateSelectedAccountDetails, updateSelectedAccountType } from '../../../../store/actionCreators/account';
import { apiErrorHandler } from '../../../../utils/general';
import { createCrypto, getSupportedCurrencies } from '../../../../store/middlewares/crypto';
import { updateSelectedCryptoAccount } from '../../../../store/actionCreators/crypto';

const supportedAccountTypes = ['cash', 'crypto', 'card'];

/**
 * Setup Wallet screen - To get account name for new account
 * @module setupWallet
 * @url /ao/account/setup
 * @see {@link https://www.figma.com/file/ndxr4b9PADQG9FMkIWp2ie/For-Solid-(Product)?node-id=22252%3A126461&t=sDqqORHs9cWTIjdP-0}
 * @returns {JSX.Element}
 */
const SetupWallet = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const navigate = useNavigate();

  const [accountLabel, updateLabel] = useState('');
  const [isAccept, toggleAccept] = useState(false);
  const [currency, updateCurrency] = useState('');
  const currencies = useAppSelector((store) => store.crypto.supportedCurrencies.data, shallowEqual);
  const { accountType, businessId, } = useAppSelector((store) => store.account.setupWalletOptions, shallowEqual);

  const {
    evolveTerms, platformTerms, walletTerms
  } = {
    evolveTerms: 'https://intercom.help/solid-2cf7e39d14f4/en/articles/5188798-evolve-bank-trust-terms-of-service',
    platformTerms: 'https://intercom.help/solid-2cf7e39d14f4/en/articles/5188803-solid-banking-platform-terms-of-service',
    walletTerms: 'https://intercom.help/solid-2cf7e39d14f4/en/articles/5188804-solid-wallet-terms-of-service',
  };

  const isValid = useMemo(() => {
    if (accountLabel.length !== 0 && isAccept && (accountType !== 'crypto' || currency.length !== 0)) {
      return true;
    }
    return false;
  }, [accountLabel, isAccept, currency, accountType]);

  useEffect(() => {
    if (accountType === 'crypto') {
      dispatch(getSupportedCurrencies())
        .then(({ data }) => {
          if (data.length !== 0) {
            updateCurrency(data[0].currency);
          }
        }).catch(apiErrorHandler);
    }
  }, [accountType, businessId]);

  const submit = useCallback(() => {
    if (isValid) {
      if (accountType !== 'crypto') {
        // card or cash
        dispatch(createAccount({
          type: accountType === 'card' ? 'cardAccount' : (businessId && businessId.length !== 0 ? 'businessChecking' : 'personalChecking'),
          label: accountLabel,
          createPhysicalCard: false,
          acceptedTerms: true,
          businessId: !businessId || businessId?.length === 0 ? undefined : businessId
        })).then((account) => {
          dispatch(updateSelectedAccountType('cash'));
          setTimeout(() => {
            dispatch(updateSelectedAccountDetails(account));
            navigate('/ao/account/ready');
          }, 0);
        }).catch(apiErrorHandler);
      } else {
        // crypto
        dispatch(createCrypto({
          label: accountLabel,
          acceptedTerms: true,
          currency,
          businessId: !businessId || businessId?.length === 0 ? undefined : businessId
        })).then((account) => {
          dispatch(updateSelectedAccountType('crypto'));
          setTimeout(() => {
            dispatch(updateSelectedCryptoAccount(account));
            navigate('/ao/account/ready');
          }, 0);
        }).catch(apiErrorHandler);
      }
    }
  }, [isValid, accountLabel, accountType, businessId, currency]);

  if (!supportedAccountTypes.includes(accountType)) {
    return null;
  }

  return (
    <Box className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
        submit();
      }}
      >
        <MiniBox
          varient="box"
          showBack
          onBack={() => {
            navigate(-1);
          }}
          heading={t('setup_wallet.title')}
          footer={(
            <Button data-testid="submit-btn" width="100%" variant="primary" disabled={!isValid} onClick={submit}>
              {t('setup_wallet.ctaText')}
            </Button>
          )}
        >
          <Box width="100%" paddingTop="15px">
            <FormControl variant="primary">
              <FormLabel variant="primary" data-testid="account-label-text">
                {t('setup_wallet.walletNameLabel')}
              </FormLabel>
              <Input data-testid="account-label" variant="primary" value={accountLabel} onChange={(e: React.ChangeEvent<HTMLInputElement>) => updateLabel(e.target.value)} />
            </FormControl>

            {accountType === 'crypto' ? (
              <FormControl variant="primary" paddingTop="15px">
                <FormLabel variant="primary">
                  {t('setup_wallet.cryptocurrency')}
                </FormLabel>
                <Select data-testid="crypto-currency-select" variant="primary" onChange={(e) => updateCurrency(e.target.value)} value={currency}>
                  {currencies.map((c) => <option key={`crypto-${c.currency}`} value={c.currency}>{c.label}</option>)}
                </Select>
              </FormControl>
            ) : null}

            <Box className={style.sendByCardContainer}>
              <Flex alignItems="start" gap="10px" pt="24px">
                <Checkbox
                  data-testid="terms-checkbox"
                  variant="primary"
                  onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                    toggleAccept(e.target.checked);
                  }}
                />
                <Box style={{ transform: 'translateY(-4px)' }}>
                  <Text variant="regular" data-testid="terms-text" className="text-light" textAlign="left">
                    {accountType === 'crypto'
                      ? (
                        <Trans t={t} i18nKey="setup_wallet.crypto_terms_message" components={{ 1: <Link variant="primary" href="https://assets.tastyworks.com/production/documents/clearing_cryptocurrency_customer_agreement.pdf" isExternal />, 2: <Link variant="primary" href="https://seedcx.zendesk.com/hc/en-us/articles/360009900494-Privacy-Policy-" isExternal />, 3: <Link variant="primary" href="https://help.solidfi.com/hc/en-us/sections/7101232391963-Zero-Hash-Disclosures" isExternal /> }} />
                      ) : (
                        <>
                          <div style={{ marginBottom: '16px' }}>{t('setup_wallet.conditionsAgree')}</div>
                          <div>
                            <Link variant="primary" href={evolveTerms} isExternal>{t('setup_wallet.evoleBank')}</Link>
                          </div>
                          <div>
                            <Link variant="primary" href={platformTerms} isExternal>{t('setup_wallet.solidPlatform')}</Link>
                          </div>
                          <div>
                            <Link variant="primary" href={walletTerms} isExternal>{t('setup_wallet.sendByMail')}</Link>
                          </div>
                        </>
                      ) }
                  </Text>
                </Box>
              </Flex>

            </Box>

            {accountType !== 'crypto'
              ? <Text data-testid="footer-desc" variant="small" mb="14px">{t('setup_wallet.footerDesc')}</Text> : null }
          </Box>
        </MiniBox>
      </form>
    </Box>
  );
};

export default SetupWallet;
